import React, { useState, useEffect } from 'react';
import './cookiesNotice.css';

const CookieNotice = () => {
  const [showNotice, setShowNotice] = useState(true);

  useEffect(() => {
    // Impide que el usuario navegue hasta que haya aceptado o rechazado las cookies
    if (showNotice) {
        document.body.style.overflow = 'hidden';
      } else {
        document.body.style.overflow = 'auto'; // Restaura el scroll
      }
  
      return () => {
        document.body.style.overflow = 'auto'; // Restaura el scroll en caso de desmontaje del componente
      };
    }, [showNotice]);

  const handleAcceptCookies = () => {
    // Guarda la preferencia del usuario cuando acepta las cookies
    // Aquí puedes utilizar una variable de estado o algún mecanismo de persistencia de datos
    // para recordar la elección del usuario

    // Oculta el aviso de cookies
    setShowNotice(false);
  };

  const handleRejectCookies = () => {
    // Tomar una acción específica cuando el usuario rechaza las cookies
    // Por ejemplo, puedes limitar ciertas funcionalidades o no almacenar cookies

    // Oculta el aviso de cookies
    setShowNotice(false);
  };

  if (!showNotice) {
    return null; // No mostrar el aviso si showNotice es falso
  }

  return (
    <div className={`cookie-notice ${showNotice ? 'show' : ''}`}>
      <div className="cookie-notice-content">
        <p>
          Utilizamos cookies para mejorar tu experiencia en nuestro sitio web. Al continuar navegando,
          aceptas nuestra política de cookies. <a href="/politica-cookies">Más información</a>
        </p>
        <div className="cookie-notice-buttons">
          <button onClick={handleAcceptCookies}>Aceptar</button>
          <button onClick={handleRejectCookies}>Rechazar</button>
        </div>
      </div>
    </div>
  );
};

export default CookieNotice;
